/**
 * WordPress dependencies
 */
import { controls, registerStore } from '@wordpress/data';

/**
 * External dependencies
 */
import type { Maybe, OmitFirstArgs, RemoveReturnTypes } from '@nab/types';

/**
 * Internal dependencies
 */
import { reducer } from './reducer';
import * as selectors from './selectors';
import * as actions from './actions';
import { STORE_NAME } from './config';
import type { Attributes, FieldId } from './types';

export { STORE_NAME } from './config';

registerStore( STORE_NAME, {
	reducer,
	controls,
	actions,
	selectors,
} );

// ==========
// TYPESCRIPT
// ==========

type Actions = RemoveReturnTypes< typeof actions >;

type Selectors = Omit<
	OmitFirstArgs< typeof selectors >,
	'getAttributes' | 'getValue'
> & {
	readonly getAttributes: < A extends Attributes = Attributes >(
		fieldId: FieldId
	) => Maybe< A >;
	readonly getValue: < T >( fieldId: FieldId ) => Maybe< T >;
};

declare module '@wordpress/data' {
	function dispatch( key: typeof STORE_NAME ): Actions;
	function select( key: typeof STORE_NAME ): Selectors;
}
