/**
 * External dependencies
 */
import type { Maybe } from '@nab/types';

/**
 * Internal dependencies
 */
import type { State, FieldId, Attributes } from './types';

export function getValue< T >( state: State, field: FieldId ): Maybe< T > {
	return state.values[ field ] as T;
} //end getValue()

export function getAttributes< A extends Attributes = Attributes >(
	state: State,
	field: FieldId
): Maybe< A > {
	return state.attributes[ field ] as Maybe< A >;
} //end getAttributes()
